</template>

<script>
    import nav from '../components/common/nav';
    export default {
        name: "account",

        data:function(){

            return {

                user:{
                    customer_id:0,
                    nickname:'',
                    avatar:'',
                }
            }
        },

        methods:{

            check_login:function(){
                var t = this;
                var url = this.config.app_url + '&r=member.member.status';

                this.axios.get( url ).then(function( res ){
                    if( res.data.status ){
                        //todo 跳转页面
                        t.$router.push('/login');
                    }else{
                        t.user.customer_id = res.data.customer_id;
                        t.user.nickname = res.data.nickname;
                        t.user.avatar = res.data.avatar;
                    }

                });
            },
        },

        created:function(){

            this.check_login();

        },
        components:{
            'bottom-nav':nav
        }
    }
</script>

