<template>
    <div class="checkout-checkout">
        <div class="layui-layer-title" style="cursor: move;">地址列表</div>
        <span class="layui-layer-setwin">
            <a class="layui-layer-ico layui-layer-close layui-layer-close1" href="#/checkout"></a></span>

        <div class="quick-checkout-wrapper">

            <div class="checkout-section" id="shipping-address-section">

                <div id="shipping-existing">
                    <ul>
                        <li class=" block" v-for="item in list"
                            v-bind:class="{'active':item.is_default==1}">
                            <div class="item" v-on:click="check_address(item)">
                                <i class="fa fa-map-marker" aria-hidden="true"></i>
                                {{item.name}} ({{item.telephone}})<br>
                                {{item.province_name}} {{item.city_name}} {{item.area_name}}<br>
                                {{item.detail_address}}
                            </div>
                            <div class="address-action">
                                <a v-bind:href="'#/checkout/address-edit/' + item.id" class="edit" ><i class="iconfont"></i></a>
                            </div>
                        </li>
                        <li class=" block" >
                            <a class="btn btn-primary btn-block" href="#/address-edit">添加新地址</a>
                        </li>
                    </ul>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        // name: "address",
        data:function(){
            return {
                list:[],
                id:'',
            }
        },

        methods:{

            get_list:function(){
                var url =  this.config.app_url + '&r=address.get_list';
                var t = this;
                this.axios.get( url ,{}).then(function( res ){
                    t.list = res.data;
                });
            },

            check_address:function( addr ){

                // var url =  this.config.app_url + '&r=address/set_default&address_id=' + addr.id;
                // var t = this;
                // this.axios.get( url ,{}).then(function( res ){
                //
                //     //todo 成功之后 返回订单页面
                //     t.$router.push('/checkout');
                // });

                var url =  this.config.app_url + '&r=checkout.checkout.address';
                var t = this;
                var data = 'address_id=' + addr.id;
                this.axios.post( url ,data).then(function( res ){

                    if( !res.data.status ){
                        //todo 成功之后 返回订单页面
                        t.$router.push('/checkout');
                    }

                });
            }

        },

        created:function(){

            this.get_list();

        },



    }
</script>

