<script>

    import component_goods_option from './common/goods-option';
    export default {
        name: "goods",
        data:function(){

            return {

                goods_id:0,

                goods_info:{

                },
                comment:[],

                goods_number:1,



                option_panel:{
                    raw_price:'',
                    specification:[],
                    spec_obj:[],
                    notice:0,
                    show:0,
                    goto:'', // value: cart or buy
                }
            }
        },

        methods:{

            page_click:function(){

                // this.option_panel.show = 0;
                this.option_panel.notice --;

            },


            get_goods_option:function(  ){

                var t = this;

                var url = this.config.app_url + '&r=goods.specification&goods_id=' + this.goods_id;

                this.axios.get( url ).then( function( res ){

                    t.option_panel.specification = res.data.specification;
                    t.option_panel.spec_obj = res.data.spec_object;

                });

            },

            get_goods_info:function(){

                var t = this;

                var url = this.config.app_url + '&r=goods.info&goods_id=' + this.goods_id;

                this.axios.get( url ).then( function( res ){

                    t.goods_info = res.data;

                    t.option_panel.raw_price = t.goods_info.price;
                });

            },

            get_comment:function(){

                var t = this;

                var url = this.config.app_url + '&r=goods.comment&goods_id=' + this.goods_id;

                this.axios.get( url ).then( function( res ){

                    t.comment = res.data;

                });

            },

            add_to_cart:function(){

                //todo 添加到购物车
                var t = this;
                var url = this.config.app_url + '&r=cart.add';
                url += '&product_id='+this.goods_id+'&number=' + this.goods_number;

                this.axios.get( url )
                    .then(function (res) {

                        if( res.data.description ){
                            //todo 显示 购物车添加提示
                            // t.tips.message = res.data.description;
                            // t.tips.show = 1;

                            var tips_data = {
                                show:1,
                                message:res.data.description
                            };

                            t.bus.$emit('tips',tips_data);
                        }
                    })
                    .catch(function (error) {
                        console.log(error);
                    });

            },

            buy:function(){


                var t = this;
                // var url = this.config.app_url + '&r=goods.buy';
                var url = this.config.app_url + '&r=checkout.checkout.goods';
                url += '&goods_id='+this.goods_id+'&number=' + this.goods_number;

                this.axios.get( url ).then(function ( res ) {

                    if( !res.data.status ){
                        t.$router.push('/checkout');
                    }else if( res.data.description ){
                        var tips_data = {
                            show:1,
                            message:res.data.description
                        };
                        t.bus.$emit('tips',tips_data);
                    }

                }).catch(function(){

                });
            },

            change_number:function( flag ){

                flag == '-' ? this.goods_number -- : this.goods_number ++ ;

                this.goods_number = this.goods_number < 1 ? 1: this.goods_number;

            },

            open_option_panel:function( e, goto ){

                if( !this.option_panel.spec_obj.length ){

                    if( goto == 'cart'){

                        this.add_to_cart();
                    }
                    if( goto=='buy'){

                        this.buy();
                    }

                    return;
                }
                e.stopPropagation();
                this.option_panel.goto = goto;
                this.option_panel.goods_id = this.goods_id;

                this.option_panel.notice ++;
                // this.option_panel.show = 1;

            }

        },

        components:{

            'goods-option':component_goods_option
        },

        created:function(){

            this.goods_id = this.$route.params.goods_id;

            this.get_goods_info();
            this.get_goods_option();
            this.get_comment();


        },


    }
</script>

