<script>
    import nav from '../components/common/nav';
    export default {
        name: "category2",

        data:function(){

            return {

                tree:{},
                current:{}
            }
        },

        methods:{

            init:function(){

                var t = this;
                var url = this.config.app_url + '&r=category.tree';
                this.axios.get( url ).then(function( res ){

                    //todo 根据属性结构布局页面
                    t.tree = res.data;
                    for(var p in t.tree ){
                        t.current = t.tree[p];
                        // t.current = t.tree[p].id;
                        break;
                    }


                });

            }

        },

        created:function(){

            this.init();

        },

        components:{

            'bottom-nav':nav
        }

    }
</script>

